import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter, Card, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import { addSurvey, updateSurvey } from '../../../actions';
import { connect } from 'react-redux';
var Comm = require('../../../services/Comm.js');

class ActionAddSurvey extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.comm = new Comm();
        this.state = {
            modal: false
        };

        this.toggle = this.toggle.bind(this);
        this.submit = this.submit.bind(this);
        this.loadSurveyUpdate = this.loadSurveyUpdate.bind(this);
        this.callbackErr = this.callbackErr.bind(this);
    }


    toggle() {
        this.setState({
            modal: !this.state.modal
        });
    }

    submit(){
        this.toggle();
        console.log(this.title.value);
        console.log(this.description.value);

        let surveyTmp = {
            "title"         : this.title.value,
            "description"   : this.description.value,
            "theme"         : this.description.value,
            "url"           : this.url.value
        }
        
        this.comm.addSurvey(surveyTmp,this.loadSurveyUpdate,this.callbackErr);
        //this.props.dispatch(addSurvey(data));
        
    }

    loadSurveyUpdate(data) {     
        this.props.dispatch(updateSurvey(data));
    }


    callbackErr(msg) {
        console.error('Network Failure ?');
        console.error(msg);

    }

/* <Button color="secondary" size="lg" block>Add Survey</Button>
 */    render() {
        return (
            <div class="panel-body">
                <button type="button" class="btn btn-primary btn-lg btn-block" onClick={this.toggle}>Add Survey</button>
                <Button color="secondary" size="lg" onClick={this.toggle} block>Add Survey</Button>
                <Modal isOpen={this.state.modal} fade={false} toggle={this.toggle} className={this.props.className}>
                    <ModalHeader toggle={this.toggle}>Create new survey</ModalHeader>
                    <ModalBody>
                        <form>
                            <div class="form-group">
                                <label>Title</label>
                                <input class="form-control" type="text" placeholder="write your title" ref={(input) => { this.title = input; }} />
                            </div>
                            <div class="form-group">
                                <label>Theme:</label>
                                <select class="form-control" ref={(input) => this.theme = input}>
                                    <option value="beauty">Beauty</option>
                                    <option value="sport">Sport</option>
                                    <option value="fashion">Fashion</option>
                                    <option value="shopping">Shopping</option>
                                </select>
                            </div>
                            <div class="form-group">
                                <label>Description</label>
                                <input class="form-control" type="textarea" ref={(input) => { this.description = input; }} />
                            </div>
                            <div class="form-group">
                                <label>Url image</label>
                                <input class="form-control" type="textarea" ref={(input) => { this.url = input; }} />
                            </div>
                        </form>
                    </ModalBody>
                    <ModalFooter>
                        <Button color="primary" onClick={this.submit}>Submit</Button>
                        <Button color="secondary" onClick={this.toggle}>Cancel</Button>
                    </ModalFooter>
                </Modal>
            </div>
            
        );
    }
}
export default connect()(ActionAddSurvey);


