import React, { Component } from 'react';
import AbstractAnswer from '../components/AbstractAnswer';
import Answer from '../../answer/containers/Answer';
import EditMetaQuestion from '../components/EditMetaQuestion';
import NewMetaQuestion from '../components/NewMetaQuestion';
import { connect } from 'react-redux';
import { removeQuestion, changeQuestionStatus, updateSurvey } from '../../../../actions';
import { Collapse, Card, CardTitle, CardBody, Button, CardText, Row, Col } from 'reactstrap';
var Comm = require('../../../../services/Comm.js');


class Question extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);
        this.comm = new Comm();
        this.toggle = this.toggle.bind(this);
        this.remove = this.remove.bind(this);
        this.getRenderByMode = this.getRenderByMode.bind(this);
        this.getEditView = this.getEditView.bind(this);
        this.state = { collapse: false };
        this.loadSurveyUpdate = this.loadSurveyUpdate.bind(this);
        this.callbackErr = this.callbackErr.bind(this);
    }

    toggle() {
        this.setState({ collapse: !this.state.collapse });
    }
    
    remove() {
        this.comm.removeQuestion({ "id_survey": this.props.idSurvey, "id_question": this.props.idQuestion }, this.loadSurveyUpdate, this.callbackErr);
        //this.props.dispatch(removeQuestion({ "id_survey": this.props.idSurvey, "id_question": this.props.idQuestion}))
    }

    getEditView() {
        this.props.dispatch(changeQuestionStatus({ "id_question": this.props.idQuestion, "question": this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion]}))
    }

    loadSurveyUpdate(data) {
        this.props.dispatch(updateSurvey(data));
    }


    callbackErr(msg) {
        console.error('Network Failure ?');
        console.error(msg);

    }

    getRenderByMode() {
        console.log(this.props.idQuestion);
        console.log(this.props.questions_status);
        switch (this.props.questions_status[this.props.idQuestion]) {
            case "display":
                console.log(this.props.idQuestion);
                return (
                    <div>
                        <Row>
                            <Col sm="9">
                                <Card body>
                                    <CardTitle>{this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].txt}</CardTitle>
                                </Card>
                            </Col>
                            <Col sm="2">
                                <Card body>
                                    <CardText>Type : {this.props.surveys[this.props.idSurvey].questions[this.props.idQuestion].question_type}</CardText>
                                    <Button color="primary" onClick={this.toggle} style={{ marginBottom: '1rem' }}>Show Answers</Button>
                                </Card>
                            </Col>
                            <Col sm="1">
                                <Card body>
                                    <div class="btn-group-vertical">
                                        <button type="button" class="btn btn-warning glyphicon glyphicon-pencil" onClick={this.getEditView}></button>
                                        <button type="button" class="btn btn-danger glyphicon glyphicon-remove" onClick={this.remove}></button>
                                    </div>
                                </Card>
                            </Col>

                        </Row>

                        <Collapse isOpen={this.state.collapse}>
                            <Card>
                                <CardBody>
                                    <Answer mode={this.props.questions_status[this.props.idQuestion]} idSurvey={this.props.idSurvey} idQuestion={this.props.idQuestion} />
                                </CardBody>
                            </Card>
                        </Collapse>

                        
                    </div>
                )

                break;

            case "edit":
                return (
                    <div>
                        <EditMetaQuestion idSurvey={this.props.idSurvey} idQuestion={this.props.idQuestion}/>
                    </div>
                    
                )

            case "new":
                return (
                    <div>
                        <NewMetaQuestion idSurvey={this.props.idSurvey} />
                    </div>

                )

                break;

            default:
                break;

        }

    }

    render() {
        let render = this.getRenderByMode();


        return (
            <div class="panel panel-default panel-body">
                {render}  
            </div>

        );

    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        surveys: state.updateModelReducer.surveys,
        questions_status: state.selectedReducer.questions_status
    }
};

export default connect(mapStateToProps)(Question);


