import * as surveysTmp from '../source/models.json';
var ObjectID = require("bson-objectid");

const updateModelReducer = (state = { surveys: {}}, action) => {
    console.log(action);

    switch (action.type) {
        case 'UPDATE_SURVEY':

            var surveys = {};

            console.log(action.obj);

            action.obj.forEach(currentSurvey => {
                console.log(currentSurvey["_id"]);
                var questions = {};
                currentSurvey["questions"].forEach(currentQuestion => {

                    var answers = {};
                    currentQuestion["answers"].forEach(currentAnswer => {
                        var answer = {
                            "id_answer": currentAnswer["_id"],
                            "position": currentAnswer["position"],
                            "txt": currentAnswer["txt"]
                        }
                        answers[currentAnswer["_id"]] = answer;
                    });

                    var question = {
                        "id_question": currentQuestion["_id"],
                        "position": currentQuestion["position"],
                        "txt": currentQuestion["txt"],
                        "question_type": currentQuestion["type"],
                        "mandatory": currentQuestion["mandatory"],
                        "answers": answers
                    }

                    questions[currentQuestion["_id"]] = question;

                });

                var newSurvey = {
                    "id_survey": currentSurvey["_id"],
                    "title": currentSurvey["title"],
                    "description": currentSurvey["description"],
                    //"start_date": "15/12/17",
                    //"end_date": "15/01/17",
                    "survey_type": currentSurvey["survey_type"],
                    "theme": currentSurvey["theme"],
                    "status": currentSurvey["status"],
                    "questions": questions,
                }

                surveys[currentSurvey["_id"]] = newSurvey;
            });
            
            //const newState1 = { surveys: action.obj.surveys };
            const newState1 = { surveys: surveys };
            return newState1;

        case 'GET_SURVEYS':
            console.log(action.obj.surveys);
            const newState0 = { surveys: action.obj.surveys };
            return newState0;

        case 'ADD_QUESTION':
            var position = Object.keys(state.surveys[action.obj.id_survey].questions).length;
            var id_question = ObjectID.generate();

            var question = {
                "id_question": id_question,
                "position": position,
                "txt": action.obj.txt,
                "type": action.obj.type,
                "mandatory": true,
                "answers": action.obj.answers
            }

            var newSurveys = {};

            for (var i = 0; i < Object.keys(state.surveys).length; i++) {
                var key = Object.keys(state.surveys)[i];
                newSurveys[key] = state.surveys[key];
            }

            // init dictionnary
            if (Object.keys(state.surveys[action.obj.id_survey].questions).length == 0) {
                newSurveys[action.obj.id_survey].questions = { id_question : question};
            } else {
                newSurveys[action.obj.id_survey].questions[id_question] = question;

            }

            const newState3 = { surveys: newSurveys };

            return newState3;

        case 'REMOVE_QUESTION':

            var posQuestion = Number(state.surveys[action.obj.id_survey].questions[action.obj.id_question].position);
            var newSurveys = {};

            for (var i = 0; i < Object.keys(state.surveys).length; i++) {
                var key = Object.keys(state.surveys)[i];
                
                newSurveys[key] = state.surveys[key];

                if (key == action.obj.id_survey){
                    var questions = {}

                    for (var j = 0; j < Object.keys(state.surveys[key].questions).length; j++) {
                        var questionKey = Object.keys(state.surveys[key].questions)[j];
                        if (questionKey != action.obj.id_question) {
                            if (Number(state.surveys[key].questions[questionKey].position) > posQuestion) {
                                var newPosition = (Number(state.surveys[key].questions[questionKey].position) - 1).toString()
                            } else {
                                var newPosition = state.surveys[key].questions[questionKey].position;
                            }

                            var currentQuestion = {
                                "id_question": questionKey,
                                "position": newPosition,
                                "txt": state.surveys[key].questions[questionKey].txt,
                                "question_type": state.surveys[key].questions[questionKey].question_type,
                                "mandatory": state.surveys[key].questions[questionKey].mandatory,
                                "answers": state.surveys[key].questions[questionKey].answers
                            };

                            questions[questionKey] = currentQuestion;
                        }
                    }
                    newSurveys[key].questions = questions;
                }
                
            }

            const newState4 = { surveys: newSurveys };

            return newState4;

        case 'EDIT_QUESTION':

            var question = {
                "id_question": action.obj.id_question,
                "position": action.obj.position,
                "txt": action.obj.txt,
                "question_type": action.obj.type,
                "mandatory": "true",
                "answers": action.obj.answers
            }

            var newSurveys = {};

            for (var i = 0; i < Object.keys(state.surveys).length; i++) {
                var key = Object.keys(state.surveys)[i];
                newSurveys[key] = state.surveys[key];
            }

            newSurveys[action.obj.id_survey].questions[action.obj.id_question] = question;

            const newState5 = { surveys: newSurveys };

            return newState5;


        case 'ADD_SURVEY':
            var id_survey = ObjectID.generate();

            let newSurvey = {
                "id_survey": id_survey,
                "title": action.obj.title,
                "description": action.obj.description,
                //"start_date": "15/12/17",
                //"end_date": "15/01/17",
                "survey_type": "fun",
                "theme": "beauty",
                "status": "offline",
                //"questions": {},
            }

/*             axios.post('http://195.154.107.158:1337/', newSurvey)
                .then(res => {
                    console.log(res);
            });
 */

            var newSurveys = {}

            newSurveys[id_survey] = newSurvey;

            for (var i = 0; i < Object.keys(state.surveys).length; i++) {
                var key = Object.keys(state.surveys)[i];
                newSurveys[key] = state.surveys[key];
            }
         
            const newState2 = { surveys: newSurveys };

            return newState2;
        
        
        default:
            return state;
    }
}
export default updateModelReducer;


