var axios = require('axios');
var ObjectID = require("bson-objectid");

class Comm {
    constructor() {
/*         this.addr = "http://195.154.107.158:1337";
 */        this.addr = "";
    }

    toString() {
        return '';
    }

    loadSurvey(callback, callbackErr) {
        axios.get(this.addr + '/')
            .then(function (response) {
                console.log(response);
                callback(response.data);
            })
            .catch(function (error) {
                callbackErr(error);
            });

    }
    addSurvey(data, callback, callbackErr) {

        var id_survey = ObjectID.generate();

        let newSurvey = {
            "id_survey": id_survey,
            "title": data.title,
            "description": data.description,
            "survey_type": "fun",
            "theme": data.theme,
            "url": data.url,
        }

        console.log("new id: " + id_survey);
        axios.post(this.addr+'/', newSurvey)
            .then(function (response) {
                console.log(response);
                callback(response.data);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }

    editSurvey(data, callback, callbackErr) {

        let newSurvey = {
            "id_survey": data.id_survey,
            "title": data.title,
            "description": data.description,
            "survey_type": "fun",
            "theme": data.theme,
            "url": data.url,
        }

        axios.post(this.addr + '/survey/update', newSurvey)
            .then(function (response) {
                console.log(response);
                callback(response.data);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }

    addQuestion(data, callback, callbackErr) {

        var id_question = ObjectID.generate();

        var answers_array = [];

        for (var i = 0; i < Object.keys(data.question.answers).length; i++) {
            var key = Object.keys(data.question.answers)[i];
            var answer = {
                "id_answer": data.question.answers[key].id_answer,
                "answer_position": data.question.answers[key].position,
                "answer_txt": data.question.answers[key].txt
            }
            answers_array.push(answer);
        }
        var question = {
            "id_survey": data.question.id_survey,
            "id_question": id_question,
            "question_position": data.position,
            "question_txt": data.question.txt,
            "question_type": data.question.type,
            "mandatory": true,
            "answers": answers_array
        }
        console.log(question);    
        console.log("new id_question: " + id_question);
        axios.post(this.addr + '/survey/add_question', question)
            .then(function (response) {
                console.log(response);
                console.log(response.data);
                callback(response.data);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }

    editQuestion(data, callback, callbackErr) {
        var answers_array = [];

        for (var i = 0; i < Object.keys(data.answers).length; i++) {
            var key = Object.keys(data.answers)[i];
            var answer = {
                "id_answer": data.answers[key].id_answer,
                "answer_position": data.answers[key].position,
                "answer_txt": data.answers[key].txt
            }
            answers_array.push(answer);
        }
        var question = {
            "id_survey": data.id_survey,
            "id_question": data.id_question,
            "question_position": data.position,
            "question_txt": data.txt,
            "question_type": data.type,
            "mandatory": true,
            "answers": answers_array
        }
        console.log(question);

        axios.delete(this.addr + '/survey/delete_question', { "data": { "id_question": data.id_question, "id_survey": data.id_survey }})
            .then(function (response) {
                console.log(response.data);
/*                 axios.post('http://195.154.107.158:1337/survey/add_question', question)
 */                axios.post('/survey/add_question', question)
                    .then(function (response) {
                        console.log(response);
                        console.log(response.data);
                        callback(response.data);
                    })
                    .catch(function (error) {
                        callbackErr(error);
                    });
            })
            .catch(function (error) {
                callbackErr(error);
            });

    
    }

    removeQuestion(data, callback, callbackErr) {
        console.log(data);
        axios.delete(this.addr + '/survey/delete_question', { "data" : data })
            .then(function (response) {
                console.log(response.data);
                callback(response.data);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }

    removeSurvey(data, callback, callbackErr) {
        console.log(data);
        axios.delete(this.addr + '/survey/delete', { "data": data })
            .then(function (response) {
                console.log(response.data);
                callback(response.data);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }

    changeStatus(data, callback, callbackErr) {
        console.log(data);
        axios.post(this.addr + '/survey/change_status', data )
            .then(function (response) {
                console.log(response.data);
                callback(response.data);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }

    savPres(presJson, callbackErr) {
        axios.post('/savePres', presJson)
            .then(function (response) {
                console.log(response);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }


    loadPres(presId, callback, callbackErr) {
        axios.get('/loadPres')
            .then(function (data) {
                var size = Object.keys(data.data).length;
                console.log("raw data");
                console.log(data.data);
                let loadedPres = ""
                if (size > 0) {
                    console.log("key");
                    console.log(Object.keys(data.data)[0]);
                    console.log("data");
                    console.log(data.data[Object.keys(data.data)[0]]);
                    loadedPres = data.data[Object.keys(data.data)[0]];
                }
                callback(loadedPres);
            })
            .catch(function (error) {
                callbackErr(error);
            });

    }

    loadContent(callback, callbackErr) {
        axios.get('/resources_list')
            .then(function (data) {
                var size = Object.keys(data.data).length;
                let contentMap = {}
                for (var i = 0; i < size; i++) {
                    let c_obj = data.data[Object.keys(data.data)[i]];
                    contentMap[c_obj.id] = c_obj;
                    // console.log(c_obj);
                }

                callback(contentMap);
            })
            .catch(function (error) {
                callbackErr(error);
            });

    }

    savPres(presJson, callbackErr) {
        axios.post('/savePres', presJson)
            .then(function (response) {
                console.log(response);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }

    savContent(contentJson, callbackErr) {
        axios.post('/addContent', contentJson)
            .then(function (response) {
                console.log(response);
            })
            .catch(function (error) {
                callbackErr(error);
            });
    }

    fileUpload(fileC, callback, callbackErr) {
        var data = new FormData();
        data.append('file', fileC);
        axios.post('/file-upload', data)
            .then(function (response) {
                console.log(response);
                callback();
            })
            .catch(function (error) {
                callbackErr(error);
            });

    }


}

module.exports = Comm;



