import React, { Component } from 'react';
import { Collapse, Card, CardBody} from 'reactstrap';
/* import { addQuestion } from '../../../actions';
 */import { connect } from 'react-redux';
import {
    initTempQuestion, changeTypeTempQuestion, changeTxtTempQuestion, 
    changeCollapseTempQuestion, changeAnswersCollapseTempQuestion, addQuestionStatus } from '../../../../actions';

import AnswerYesNo from '../../answer/components/AnswerYesNo';
import Question from '../../question/containers/Question';


class ActionAddQuestion extends Component {
    //class constructor whith given properties
    constructor(props) {
        super(props);

        this.state = {
            content: ""
        };

        this.toggle = this.toggle.bind(this);
        this.handleQuestionChange = this.handleQuestionChange.bind(this);
        this.toggleAnswers = this.toggleAnswers.bind(this);
        this.changeChoice = this.changeChoice.bind(this);
        this.yesNoChoice = this.yesNoChoice.bind(this);
        this.multipleChoice = this.multipleChoice.bind(this);
        this.uniqueChoice = this.uniqueChoice.bind(this);

        this.props.dispatch(initTempQuestion())
    }


    toggle() {
        this.props.dispatch(changeCollapseTempQuestion());
/*         this.setState({
            collapse: !this.state.collapse
        }); */
    }

    toggleAnswers() {
        this.props.dispatch(changeAnswersCollapseTempQuestion());

/*         this.setState({
            answersCollapse: !this.state.answersCollapse,
        }); */
    }

    changeChoice(type){
        console.log(type);
        if (this.props.type == "" || this.props.type == type) {
            this.toggleAnswers();
        }

        if (this.props.type == type) {
            this.props.dispatch(changeTypeTempQuestion(""));
            this.setState({
                content : ""
            });
        } else {
            this.props.dispatch(changeTypeTempQuestion(type));
            this.props.dispatch(addQuestionStatus("temp"));
            this.setState({
                content: <Question idSurvey={this.props.idSurvey} idQuestion="temp" />
            });

        
            /* this.state.content = <Question idSurvey={this.props.idSurvey} mode="edit"/>; */
    
        }
    }

    handleQuestionChange(e) {
        this.props.dispatch(changeTxtTempQuestion(e.target.value));
    }

    yesNoChoice(){
        this.changeChoice("YesNo");
    }

    multipleChoice(){
        this.changeChoice("multiple");
    }

    uniqueChoice(){
        this.changeChoice("unique");
    }

     render() {
         /*<Question idSurvey={this.props.idSurvey} mode="edit"/>*/
        return (
            <div class="panel-body">
                <form>
                <div class="input-group">
                    <span class="input-group-addon" id="basic-addon1">?</span>
                        <input type="text" name='txt' value={this.props.txt} onChange={this.handleQuestionChange.bind(this)} class="form-control" placeholder="Question" aria-describedby="basic-addon1" />

                    <span class="input-group-btn">
                        <button type="button" class="btn btn-primary" onClick={this.toggle}>+</button>
                    </span>

                </div>
                
                <Collapse isOpen={this.props.collapse}>
                    <Card>
                        <CardBody>
                            <br/>
                                <div class="btn-group btn-group-justified" role="group" aria-label="...">
                                    <div class="btn-group" role="group">
                                        <button type="button" class="btn btn-default" onClick={this.yesNoChoice}>Yes or No</button>
                                    </div>
                                    <div class="btn-group" role="group">
                                        <button type="button" class="btn btn-default" onClick={this.multipleChoice}>Multiple choice</button>
                                    </div>
                                    <div class="btn-group" role="group">
                                        <button type="button" class="btn btn-default" onClick={this.uniqueChoice}>Unique choice</button>
                                    </div>
                                </div>
                            
                                <br />
                                <Collapse isOpen={this.props.answersCollapse}>
                                    {this.state.content}
                                    {/*<Question idSurvey={this.props.idSurvey} mode="edit" currentTxt={this.state.txt} currentType={this.state.type}/>*/}
                                </Collapse>
                          
                            
                        </CardBody>
                    </Card>
                </Collapse>
                </form>
            </div>

        );
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log(state);
    return {
        txt: state.tempQuestionReducer.question,
        type: state.tempQuestionReducer.type,
        collapse: state.tempQuestionReducer.collapse,
        answersCollapse: state.tempQuestionReducer.answersCollapse
    }
};

export default connect(mapStateToProps)(ActionAddQuestion);



